import React from "react"
import PropTypes from "prop-types"
import { Avatar, NavigationDrawer, FontIcon, ListItem } from "react-md"
import "../styles/index.scss"
import { useStaticQuery, graphql } from "gatsby"

const Layout = ({ children }) => {
  const data = useStaticQuery(graphql`
    query SiteQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  const menus = [
    {
      id: 1,
      icon: "folder",
      primaryText: "Proceeding 2017",
      secondaryText: "Access and Engagement",
      component: "a",
      href: "/proc2017/",
    },
    {
      id: 2,
      icon: "folder",
      primaryText: "Proceeding 2019",
      secondaryText:
        "Elevating Innovation for Sustainable Development of Special Needs Education",
      component: "a",
      href: "/proc2019/",
    },
  ]

  const styles = {
    content: { minHeight: "auto" },
  }

  return (
    <NavigationDrawer
      type={NavigationDrawer.DrawerTypes.FULL_HEIGHT}
      drawerTitle="Knowledgebase"
      navItems={menus.map(menu => (
        <ListItem
          key={menu.id}
          leftAvatar={<Avatar icon={<FontIcon>{menu.icon}</FontIcon>} />}
          primaryText={menu.primaryText}
          secondaryText={menu.secondaryText}
          component={menu.component}
          href={menu.href}
          rel="noopener noreferrer"
        />
      ))}
      toolbarTitle={data.site.siteMetadata.title}
      contentStyle={styles.content}
    >
      <div className="App">
        <div className="App-header">
        </div>
        <div className="App-intro">
          {children}
        </div>
      </div>
    </NavigationDrawer>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
